@import url(https://fonts.googleapis.com/css2?family=Rowdies&display=swap);
:root {
  --danger: #eebb4d;
  --blue: #eebb4d;
  --light: #221f3b;
}

body {
  background-color: #221f3b;
  background-color: var(--light);
  overflow-x: hidden !important;
}


@media screen and (max-width: 600px) {
  .navbar-brand .pollbox .form{
    overflow-x: hidden;
 
   }
 }

 .footer {
   padding-top: 1px;
   padding-bottom: 20px;
  margin-top: 60px;
  
  justify-content: center;
  text-align: center;
  text-transform: uppercase;
  color: royalblue;
  background-color:white;
  font-weight: 600;
  box-shadow: cornsilk 5px;
}

.span {
  font-weight: 900;
  color: brown;
}



@media screen and (max-width: 600px) {
  .footer{
      padding-bottom: 20px;
      padding-top: 5px;
   }
.mfooter {

  margin-top: 90px;
}
  
 }

* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    list-style: none;
  }
.container {
    margin: auto;
    width: 80%;
  }
.error {
    background-color: var(--danger);
    text-align: center;
    color: white;
    width: 70%;
    margin: 1rem auto;
    padding: 0.5rem 0;
    border-radius: 0.5rem;
  }
.navbar {
    background-color:white;
    padding: 1rem 0;
  }
  
  .navbar-brand {
   
    color: var(--blue);
    text-transform: uppercase;
 

   justify-content: left;


  }

  img {
    height:105px;
    width: 105px;
  }
  
  .navbar-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: center;
    align-items: center;
  }
  
  .navbar-item {
    color: royalblue;
    text-decoration: none;
    cursor: pointer;
    margin-top: 1px;
    
  }

  .logout {
    background-color: var(--light);
    color: yellow;
    border-radius: 0.5rem;
    text-transform: uppercase;
    padding: 0.5rem;

  }  
  
  .navbar-user {
    margin-top: 0.5rem;
    text-align: right;
    color: var(--light);
    font-style: italic;
  }

  .addnew {
    width: 180px;
    height: 50px;
    background-color: rebeccapurple;
    color: yellow;
    border-radius: 0.5rem;
    text-transform: capitalize;
    font-style: italic;
 margin-left: -20px;
    margin-top: 2px;
    font-size: x-large;
   box-shadow: skyblue;
   justify-content: center;
  }  

  @media screen and (max-width: 600px) {
   .addnew{
   width: 100px;
   
      display: block;
      font-size: large;
      margin-left: -10px;
      margin-top: 0px;
      justify-content: space-around;
    }
  }

  @media screen and (max-width: 600px) {
    img{

        height:75px;
justify-content: left;
       font-size: large;
       margin-left: -5px;
   
     }
   }

.button {
    background-color: blueviolet;
    border: 2px solid var(--blue);
    border-radius: 0.5rem;
    color: white;
    font-size: 1rem;
    margin: 1rem;
    padding: 0.2rem 1rem;
    transition: all 0.4s ease-out;
  }
  
  .button:hover {
    background-color:  var(--blue);
  }
  
  .buttons_center {
    display: flex;
    justify-content: center;
  }
.polls li {
    margin-top: 2px;
    background-color: var(--light);
    color: var(--blue);
    padding: 0.5rem;
    text-align: center;
  }
  
  .polls li:nth-child(odd) {
    background-color:#96bb7c;
    color:#221f3b;
  }
  
  .polls li:hover {
    border: 2px solid var(--blue);
  }
  
  .poll-title {
    color: var(--blue);
    font-size: 2rem;
    margin-top: 2rem;
    text-align: center;
    text-transform: uppercase;

  }

  .pollbox {
    justify-content: center;
    text-align: center;
    margin-top: 50px;
  }

  .next{
    display: grid;
    flex-direction: column;
  }


  .check {
 
    background-color: yellow;
    border: 2px solid ;
    border-radius: 0.5rem;
    color: white;
    text-align: center;
    padding: 5px;
    transition: all 0.4s ease-out;
    margin-top: 30px;
  }
.form {
    width: 60%;
    margin: 6rem auto;
    background-color: var(--blue);
    padding: 1rem;
    border-radius: 0.4rem;
  }
  
  .form-input {
    width: 100%;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0;
    border: 1px solid var(--light);
    border-radius: 0.4rem;
    box-sizing: border-box;
    outline: none;
    font-size: 1rem;
  }
  
  .form-label {
    color: royalblue;
    text-transform: uppercase;
    font-size: 0.8rem;
  }

